import { Injectable, OnInit } from '@angular/core';
import { LoadingController } from '@ionic/angular';

@Injectable({
  providedIn: 'root'
})
export class LoadingService implements OnInit{
  dustyLoading: HTMLIonLoadingElement;
  usuarioLoading: HTMLIonLoadingElement;
  newTeachLoading: HTMLIonLoadingElement;
  constructor(private loadingCtrl: LoadingController) { }
  ngOnInit() {
  }
  async mapLoading(){
    this.dustyLoading = await this.loadingCtrl.create({
      message:"Dusty está trabajando",
      spinner:"bubbles",
    })
    return await this.dustyLoading.present();
  }
  async userLoading(){
    this.usuarioLoading = await this.loadingCtrl.create({
      message:"Vamos a ver quién eres...",
      spinner:"bubbles",
    })
    return await this.usuarioLoading.present();
  }
  async newTeacherLoading(){
    this.newTeachLoading = await this.loadingCtrl.create({
      message:"Vamos a ver quién eres...",
      spinner:"bubbles",
    })
    return await this.newTeachLoading.present();
  }
}
