import { Component, OnInit } from '@angular/core';
import { Platform } from '@ionic/angular';
import { BehaviorSubject } from 'rxjs';
import { UserService } from './user.service';
import {Storage} from '@ionic/storage-angular';


const TOKEN_KEY= 'auth-token'; 

@Component({
  selector: 'app-register',
  templateUrl: './registro.component.html',
  styleUrls: ['./registro.component.scss'],
  //providers: [UserService]
})
export class RegisterComponent implements OnInit {
  input;
  authenticationState= new BehaviorSubject(false);
  userType= new BehaviorSubject('undefined');

  constructor(private userService: UserService, private plt: Platform,public storage: Storage) { 
    this.plt.ready().then(()=>{
      this.checkToken(); 
     });
  }

  ngOnInit() {
    this.input= {
      username:'',
      password:'',
      email:''
    };  
  }
  registerUser(){
    this.userService.registerTeacher(this.input).subscribe(
      response => {
        console.log(response)
        alert('User '+ this.input.username +' has been created!')},
      error=> {console.log(this.input,'error',error)} 
      );
  } 
   async loginUser(){
    (await (this.userService.loginUser(this.input))).subscribe(
      async response => {
        (await this.storage.create()).set(TOKEN_KEY, '12345').then(() => {
          this.authenticationState.next(true); 
          this.userType.next("alumno");
        }
      );
        console.log(response,this.storage,this.userType)
        alert('User '+ this.input.username +' logged!')
      },
      error=> {console.log('error',error)} 
      );
    
  } 
  isAuthenticated(){
    return this.authenticationState.value;
  }
  
  checkToken(){
    return this.storage.get(TOKEN_KEY).then(res =>{
      if(res){
        this.authenticationState.next(true);
      }   
    });
  }
}
