import { Component, OnInit } from '@angular/core';
import { Router} from '@angular/router';
import { PopoverController } from '@ionic/angular';
import { Ejercicio } from 'src/app/members/student/Others/Models/ejercicio.model';
import { EjerciciosService } from '../../../Services/ejercicios/ejercicios.service';

@Component({
  selector: 'app-hints',
  templateUrl: './hints.component.html',
  styleUrls: ['./hints.component.scss'],
})
export class HintsComponent implements OnInit {
  i=1;
  hint;
  ejercicio: Ejercicio;
  selected:string;
  constructor(public router: Router, private ejerciosService: EjerciciosService, public popoverController: PopoverController) { }

  ngOnInit() {
    //Look the url and take the exercise JSON.
    const url_ex= this.router.url;
    this.ejercicio = this.ejerciosService.getEjercicio(url_ex);
    this.hint= this.ejercicio.hints[0]
    var numberHints= String(this.i)
    localStorage.setItem('numberHints',numberHints)
  }
  moreHints(){
    this.hint= this.ejercicio.hints[this.i]
    this.i++
    var numberHints= String(this.i)
    localStorage.setItem('numberHints',numberHints)
  }
}