import { interval } from "rxjs";
import { Injectable } from '@angular/core';


@Injectable({
  providedIn: 'root'
})

export class CounterService {
    time: any;
    tiempo: number;
    setCounter(){    
        var counter= interval(1000)
            this.time=counter.subscribe((n) =>{
                this.tiempo=n
                //console.log(this.tiempo);
            }
        );
        return this.time, this.tiempo
    }
}
