import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import {Storage} from '@ionic/storage-angular';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  private api = "https://khalil-app-306611.ew.r.appspot.com/KhalilApp";
  
  httpHeaders = new HttpHeaders({'Content-Type': 'application/json'});
  authenticationState= new BehaviorSubject(false);
  userType= new BehaviorSubject('undefined');


  constructor(private http:HttpClient,public storage: Storage) { }

  registerTeacher(teacherData):Observable<any>{
    return this.http.post(`${this.api}/register_teacher`,teacherData,{headers: this.httpHeaders});
  }
  async loginUser(userData){
    return this.http.post(`${this.api}/auth/`,userData,{headers: this.httpHeaders});
    
  }
  
}
